* {
  box-sizing: border-box;
  
}

body {

  margin: 0;
  background-position: left;
  background-size: cover;
  width: 100%;
  min-height: 100vh;
  z-index: -1;
}

/* Navbar */

.navbar {
  display: flex;
  justify-content: space-between;
  /* box-shadow: 0px 2px 6px rgb(26, 26, 26); */
  align-items: center;
  background: rgba(109, 109, 109, 0.1);
  font-size: 1.2rem;
  padding: 0px 5px 0px 5px;
}

.navbar-nav {
  margin-left: auto;
  margin-bottom: 10px;
  margin-top: 10px;
}

.navbar-nav .nav-link{
  color: #fff;
  font-family: Raleway;
  font-weight: 200;
  transition: 0.5s;
  padding: 10px;
}

.navbar-nav .nav-link:hover {
  background: rgb(255, 255, 255, 0.4);
  border-radius: 4px 4px 4px 4px;
}

.navbar-brand{
  color: #fff;
  font-family: 'Oleo Script Swash Caps', cursive;
  font-size: 2rem;
}

.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
    outline: none;
    box-shadow: none;
}

/* App Body */

.current--forecast::-webkit-scrollbar {
  display: none;
}

/* Current Forecast */

.current--all {
  margin: 10px;
  margin-top: 20px;
}

.current--search {
  text-align: center;
  margin: 10px 20px 0px 20px;
}

.current--search > input {
  width: 30vh;
  margin-right: 5px;
  padding: 5px 0px 5px 4px;
  border-radius: 4px;
  border: 1px solid black
}

.current--search > button {
  padding: 6px 15px 6px 15px;
  border-radius: 4px;
  transition: 0.4s;
  font-family: Quicksand;
  font-weight: 700;
  background: rgb(32, 32, 32);
  color: white;
  border: none;
}

.current--search > button:hover {
  background: rgba(64, 127, 146);
}

input:focus {
  outline: none;
}

button:focus {
  outline: none;
}

.current--top {
  display: flex;
  justify-content: center;
}

.current--bottom {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.current--quadOne {
  border-radius: 10px 0px 0px 0px;
}

.current--quadTwo {
  border-radius: 0px 20px 0px 0px;
}

.current--quadThree {
  border-radius: 0px 0px 0px 20px;
}

.current--quadFour {
  border-radius: 0px 0px 20px 0px;
}

.current--quadTwo {
  justify-content: center;
  align-items: center;
  text-align: center;
  
}

.current--quad {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 215px;
  width: 240px;
  border: 1px solid black;
  transition: 0.4s;
  color: white;
  background-color: rgba(141, 141, 141, 0.7);
}

.current--quad:hover {
  background: rgba(64, 127, 146, 0.8);
  
}

.current--forecast{
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: scroll;
}

/* day.js = Quadrant One */

.quadOne--container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  font-family: Quicksand;
  font-weight: 500;
}

.today--day {
  font-size: 1.5rem;
}

.today--date {
  margin: 10px 0px 10px 0px;
  font-size: 1rem;
}

/* weather.js = Quadrant Two */

.weatherImg {
  width: 95%;
  object-fit: cover;
  border-radius: 20px;
  object-position: center;
}

/* time.js = Quadrant Three */

.quadThree--container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Quicksand;
  font-weight: 500;
  font-size: 1.6rem;
  margin: auto ;
  margin-top: 50px;
}

.quadThree--time {
  padding-bottom: 27px;
  border-bottom: 2px dotted black;
  max-width: 150px;
  font-weight: 700;
}

.quadThree--sun {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 40px;
  font-family: Quicksand;
  font-weight: 500;
  font-size: 1rem;

}

.quadThree--sunRise {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 5px;
}

.quadThree--sunSet {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.sunImg {
  width: 2.1rem;
  margin-right: 5px;
}

/* temperature.js = Quandrant Four */

.quadFour {
  font-family: Quicksand;
  font-weight: 700;
  font-size: 1rem;
}

.quadFour--temperature {
  display: flex;
  flex-direction: column;
}

.quadFour--celcius{
  display: flex;
  width: 100%;
  padding: 0px 10px 10px 10px;
  border-bottom: 2px dotted black;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;

}

.quadFour--farenheit {
  display: flex;
  width: 100%;
  padding: 0px 10px 10px 10px;
  border-bottom: 2px dotted black;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;

}

.quadFour--weather {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 150px;
  padding: 0px 10px 0px 10px;
  align-items: center;
  justify-content: center;
  text-align: center;
  
}

/* Future Forecast Cards */

.future--container {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Quicksand;
  font-weight: 500;
  font-size: 0.7rem;
  margin: 15px 10px 45px 10px;
  border-radius: 10px 10px 10px 10px;
  transition: 0.4s;
  background-color: rgba(141, 141, 141, 0.7);
  color: white;
}

.future--container:hover {
  background: rgba(64, 127, 146, 0.8);
  position: relative;
  transform: translateY(-7%);
}

.future--left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 5rem;
  height: 12rem;
}

.future--top--left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  width: 100%;
  height: 50%;
  border-radius: 10px 0px 0px 0px;
}

.future--day {
  font-size: 0.8rem;
  font-weight: 700;
}

.future--bottom--left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50%;
  border: 1px solid black;
  border-radius: 0px 0px 0px 10px;
}

.future--bottom--left > img {
  width: 100%;
}

.future--right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  width: 5rem;
  height: 12rem;
  border-radius: 0px 10px 10px 0px;
  font-weight: 700;
  font-size: 0.8rem;
}

.future--celsius{
  display: flex;
  width: 75%;
  padding: 0px 10px 10px 10px;
  border-bottom: 2px dotted black;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;

}

.future--farenheit {
  display: flex;
  width: 75%;
  padding: 0px 10px 10px 10px;
  border-bottom: 2px dotted black;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;

}

.future--condition {
  display: flex;
  flex-direction: column;
  width: 75%;
  max-width: 150px;
  padding: 0px 10px 0px 10px;
  align-items: center;
  justify-content: center;
  text-align: center;
  
}

/* Currency Converter */

.currency--container {
  font-family: Quicksand;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.currency--title {
  font-size: 2rem;
  font-family: Raleway;
  font-weight: 200;
  color: whitesmoke;
  width: 100%;
  margin: auto;
  text-align: center;
  margin-bottom: 30px;
  background-color: rgba(31, 31, 31, 0.4);
  padding: 40px 0px 40px 0px  ;
}

.currency--base {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 8px 8px 8px 8px;
}

.currency--baseSelect {
  width: 30vw;
  min-width: 220px;
  padding: 5px 0px 5px 0px;
  margin: 5px 5px 0px 5px;
  border: none;
  background-color: rgb(0,0,0,0.7);
  color: white;
  font-weight: 700;
  font-size: 1rem;
  transition: 0.4s;
  border-radius: 8px 8px 0px 0px;
}

.currency--baseInput {
  width: 30vw;
  min-width: 220px;
  padding: 10px 0px 10px 0px;
  border: none;
  color: white;
  background-color: rgb(51, 51, 51, 0.6);
  text-align: center;
  font-family: Quicksand;
  font-size: 1.2rem;
  border-radius: 0px 0px 8px 8px;
}

.currency--image{
  text-align: center;
  margin-top: 20px;
}

.currency--tradingImg {
  width: 45px;
}

.currency--targetSelect {
  width: 30vw;
  min-width: 220px;
  padding: 5px 0px 5px 0px;
  margin: 20px 5px 0px 5px;
  border: none;
  background-color: rgb(0,0,0,0.7);
  color: white;
  font-weight: 700;
  font-size: 1rem;
  transition: 0.4s;
  border-radius: 8px 8px 0px 0px;
}

.currency--target {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.currency--targetAmount {
  width: 30vw;
  height: 40px;
  min-width: 220px;
  padding: 10px 0px 10px 0px;
  border: none;
  color: white;
  background-color: rgb(51, 51, 51, 0.6);
  border-radius: 0px 0px 8px 8px;
  font-size: 1.2rem;
}

.currency--preset {

  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin-bottom: 60px;

}

.currency--presetCards {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: auto;
  font-family: Quicksand;
  font-weight: 500;
  width: 70%;
}

/* Preset currencies */

.preset--container {
  margin: auto;
  margin-top: 15px;
  font-family: Quicksand;
  border-radius: 8px 8px 8px 8px;
  transition: 0.4s;
  width: 40%;
  min-width: 110px;
}

.preset--container:hover {
  background: rgba(0, 250, 250, 0.7);
  transform: scale(1.1);
}

.preset--top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  padding: 5px 10px 5px 5px;
  color: white;
  background-color: rgba(22, 22, 22, 0.6);
  border: none;
  border-radius: 8px 8px 0px 0px;
  font-weight: 700;
  font-size: 1.15rem;
}

.preset--bottom {
  color: white;
  background-color: rgb(51, 51, 51, 0.6);
  border-radius: 0px 0px 8px 8px;
  height: 50px;
  font-size: 1.1rem;
}

.preset--rate {
  padding: 20px 0px 0px 10px;
}

/* Footer */

footer {
  display: flex;
  justify-content: space-between;
  height: 40px;
  position: fixed;
  text-align: center;
  bottom: 0;
  left: 0;
  padding: 10px;
  font-family: 'Quicksand';
  font-size: 0.8rem;
  background-image: linear-gradient(to right,#1a1a1a, #333333);
  color: white;
  width: 100%;
}
  
  

